import colors = require('colors');
import glob = require('glob');
import fs = require('fs');
import * as path from 'path';
import { log } from '@microsoft/gulp-core-build';

import IFile from '../models/packageXml/IFile';

/** Loads a directory of files into IFile objects */
export function getFiles(cwd: string, globs: string, foundMessage: string = 'Found file: '): Promise<IFile[]> {
  return new Promise<IFile[]>((complete: (files: IFile[]) => void, reject: (error: Error) => void) => {
    glob(globs, {
      cwd: cwd
    }, (error: Error, files: string[]) => {
        if (error) {
          reject(error);
        }
        complete((files || []).map( (filename: string): IFile => {
          log(foundMessage + filename);
          return {
            filename: filename,
            contents: fs.readFileSync(path.join(cwd, filename))
          };
        }));
      });
  });
}

/** Reads the list of relative path from a specified folder */
export function getRelativeFilePaths(cwd: string, globs: string, foundMessage: string = 'Found file: '):
  Promise<string[]> {
  return new Promise<string[]>((complete: (files: string[]) => void, reject: (error: Error) => void) => {
    glob(globs, {
      cwd: cwd
    }, (error: Error, files: string[]) => {
      if (error) {
        reject(error);
      }
      complete((files || []).map((filename: string) => {
        filename = filename;
        log(colors.cyan(foundMessage + filename));
        return filename;
      }));
    });
  });
}